:root {
    --yellow: #ffd100;
    --black: #130b0c;
    --white: #ffffff;
    --gray: #8b8887;
}

.container {
    display: flex;
    height: 60px;
}

.slider {
    position: relative;
    width: 200px;
}

.slider__track,
.slider__range,
.slider__left-value,
.slider__right-value {
    position: absolute;
}

.slider__track,
.slider__range {
    border-radius: 3px;
    height: 5px;
}

.slider__track {
    background-color: rgba(78, 77, 77, 0.6);
    width: 100%;
    z-index: 1;
}

.slider__range {
    background-color: var(--yellow);
    z-index: 2;
}

.slider__left-value,
.slider__right-value {
    color: var(--gray);
    font-size: 12px;
    margin-top: 20px;
}

.slider__left-value {
    left: 6px;
}

.slider__right-value {
    right: -4px;
}

/* Removing the default appearance */
.thumb,
.thumb::-webkit-slider-thumb {
    -webkit-appearance: none;
    -webkit-tap-highlight-color: transparent;
}

.thumb {
    pointer-events: none;
    position: absolute;
    height: 0;
    width: 200px;
    outline: none;
}

.thumb--left {
    z-index: 3;
}

.thumb--right {
    z-index: 4;
}

/* For Chrome browsers */
.thumb::-webkit-slider-thumb {
    background-color: var(--yellow);
    border: none;
    border-radius: 50%;
    box-shadow: 0 0 1px 1px var(--yellow);
    cursor: pointer;
    height: 18px;
    width: 18px;
    margin-top: 4px;
    pointer-events: all;
    position: relative;
}

/* For Firefox browsers */
.thumb::-moz-range-thumb {
    background-color: var(--yellow);
    border: none;
    border-radius: 50%;
    box-shadow: 0 0 1px 1px var(--yellow);
    cursor: pointer;
    height: 18px;
    width: 18px;
    margin-top: 4px;
    pointer-events: all;
    position: relative;
}